<template>
    <div>
        <h1 class="header">Vue Music</h1>
        <div class="container">
            <div class="player">
                <section class="music-info">
                    <h1 class="song-title">{{songs[currentIndex].title}}</h1>
                    <h2 class="song-artist">{{songs[currentIndex].artist}}</h2>
                </section>

                <section class="control-buttons">
                    <button @click="previousSong()">&#9198;</button>
                    <button v-show="playing" @click="pauseSong()">&#9209;</button>
                    <button v-show="!playing" @click="playSong()">&#9205;</button>
                    <button @click="nextSong()">&#9197;</button>
                </section>

                <section class="progress">
                    <div class="progress-bar-background">
                        <div class="progress-bar" :style="{width: percentageWidth}" />
                    </div>
                </section>
            </div>
        </div>
            <section class="playlist">
                <h1 class="playlist-title">PLAYLIST</h1>
           
                <div
                    class="playlist-song"
                    v-for="song in songs"
                    :key="song.key"
                    :class="{playlistSongCurrent: song.key==currentIndex}"
                    @click="chooseSong(song.key)"
                >
                <div >{{song.title}}</div>
                <div style="font-size: 15px; color: #BBB;">{{song.artist}}</div>
                </div>

                
            </section>
        </div>
</template>

<script>
export default {
    name: "HelloWorld",
    data() {
        return {
            musicTitle: "Rock i am Rock",
            playing: false,
            time: 0,
            intervalSet: false,
            musicDuration: 0,
            currentIndex: 0,
            percentageWidth: "0%",
            songs: [
                {
                    key: 0,
                    title: "Renegades",
                    artist: "X Ambassadors",
                    songSrc: require("../assets/X Ambassadors - Renegades (Lyric Video).mp3")
                },
                {
                    key: 1,
                    title: "Legendary",
                    artist: "Welshly Arms",
                    songSrc: require("../assets/WELSHLY ARMS LEGENDARY LYRICS.mp3")
                },
                {
                    key: 2,
                    title: "Stay Wide Awake",
                    artist: "Eminem",
                    songSrc: require("../assets/EMINEM - Stay Wide Awake (LYRICS).mp3")
                },
                {
                    key: 3,
                    title: "Dangerous",
                    artist: "Neffex",
                    songSrc: require("../assets/Neffex - Dangerous.mp3")
                },
                {
                    key: 4,
                    title: "Power",
                    artist: "Kanye West",
                    songSrc: require("../assets/Kayne West - Power.mp3")
                },
                {
                    key: 5,
                    title: "Rock I am Rock",
                    artist: "Rock",
                    songSrc: require("../assets/Rock - I am Rock.mp3")
                },
                
            ],
            player: new Audio()
        };
    },
    created() {
        this.loadNewSong();
    },
    methods: {
        playSong() {
            this.musicDuration = this.player.duration;
            this.player.currentTime = this.time;
            this.player.play();
            this.playing = true;
            if (!this.intervalSet) {
                this.interval = setInterval(
                    () => (this.time = this.player.currentTime),
                    1000
                );
                this.intervalSet = true;
            }
        },

        pauseSong() {
            this.player.pause();
            this.playing = false;
        },

        loadNewSong() {
            this.player.src = this.songs[this.currentIndex].songSrc;
        },

        nextSong() {
            if (this.currentIndex === this.songs.length - 1) this.currentIndex = 0
            else this.currentIndex++

            this.loadNewSong();
            this.checkifLoaded();
        },
        previousSong() {
            if (this.time > 1){
                this.player.currentTime = 0
                this.time = 0
            }
            else {
                if (this.currentIndex === 0)
                    this.currentIndex = this.songs.length - 1
                else this.currentIndex--

                this.loadNewSong();
                this.checkifLoaded();
            }
        },
        checkifLoaded() {
            if (this.player.readyState == 4) {
                clearInterval(this.i);
                this.time = 0;
                this.playSong();
            } else {
                clearInterval(this.i);
                this.i = setInterval(this.checkifLoaded, 500);
            }
        },
        chooseSong(index){
            this.currentIndex = index;
            this.loadNewSong();
            this.checkifLoaded();
        }
    },
    watch: {
        time: function() {
            this.percentageWidth = ((this.time / this.musicDuration) * 100).toString() + "%";
            if (this.player.ended)
                this.nextSong()
                
        }
    }
};
</script>

